body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


* {
	box-sizing: border-box;
	font-family: sans-serif;
  }
  
  ul,
  li {
	margin: 0;
	padding: 0;
	list-style-type: none;
  }
  
  a {
	text-decoration: none;
  }
  
  header {
	padding: 40px 20px ;
  }
.navbar {
  padding: 20px;
  margin: 0 auto;
  max-width: 1440px;
}

.navbar ul {
  position: relative;
  display: flex;
  justify-content: space-evenly;
  border-bottom: 2px solid #dbdbdb;
  padding-bottom: 10px;
}

.navbar ul li {
  display: inline-block;
}

.navbar ul li a {
  color: #b2b2b2;
  letter-spacing: 1px;
  font-size: 1em;
  padding-bottom: 10px;
}

.navbar ul li a:hover {
  color: #6ca0c5;
}

.navbar ul li a.active {
  color: #1f1f1f;
}

.navbar ul li a.active:hover {
  color: #1f1f1f;
  cursor: default;
}

.navbar .underline {
  position: absolute;
  bottom: -2px;
  height: 2px;
  background-color: #1f1f1f;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

@media only screen and (max-width: 980px) {
  .navbar ul {
    flex-direction: column;
    border-bottom: none;
  }

  .navbar ul li {
    padding: 15px 0;
  }

  .navbar ul li a {
    padding-bottom: 4px;
  }

  .navbar ul li a.active {
    border-bottom: 2px solid #1f1f1f;
  }

  .navbar .underline {
    display: none;
  }
}
